import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { graphql } from 'gatsby';
import { useInView } from 'react-intersection-observer';

import Banner from '../components/banner';
import Layout from '../components/layout';
import SEO from '../components/seo';
import PostCard from '../components/postCard';
import SideBar from '../components/SideBar';
import '../utils/css/index.scss';

const Tags = ({ pageContext, data, location }) => {
    const [page, setPage] = useState(1);
    const siteTitle = data.site.siteMetadata.title;
    const posts = data.allMarkdownRemark.edges;

    const { social, author } = data.site.siteMetadata;
    let postCounter = 0;
    const { tag } = pageContext;
    const { ref, inView, entry } = useInView({
        /* Optional options */
        threshold: 0
    });
    useEffect(() => {
        if (inView && posts.length > page * 10) {
            setPage(prevPage => prevPage + 1);
        }
    }, [inView]);
    return (
        <Layout location={location} title={siteTitle} social={social} author={author}>
            <SEO title={tag} />
            {/* <Bio /> */}
            <Banner imgFluid={data.file.childImageSharp.fluid} />
            <div className="post-tags">Tag : {tag}</div>
            <div className="index-container">
                <SideBar />
                <div className="post-feed">
                    {posts.map(({ node }, index) => {
                        postCounter++;
                        if (index > page * 10) {
                            return null;
                        }
                        return (
                            <PostCard
                                key={node.fields.slug}
                                count={postCounter}
                                node={node}
                                postClass={`post`}
                            />
                        );
                    })}
                </div>
            </div>
            <div ref={ref}></div>
        </Layout>
    );
};

Tags.propTypes = {
    location: PropTypes.object,
    pageContext: PropTypes.shape({
        tag: PropTypes.string.isRequired
    }),
    data: PropTypes.shape({
        allMarkdownRemark: PropTypes.shape({
            totalCount: PropTypes.number.isRequired,
            edges: PropTypes.arrayOf(
                PropTypes.shape({
                    node: PropTypes.shape({
                        frontmatter: PropTypes.shape({
                            title: PropTypes.string.isRequired
                        }),
                        fields: PropTypes.shape({
                            slug: PropTypes.string.isRequired
                        })
                    })
                }).isRequired
            )
        })
    })
};

export default Tags;

export const pageQuery = graphql`
    query($tag: String) {
        site {
            siteMetadata {
                title
                author
            }
        }
        file(relativePath: { eq: "banner.png" }) {
            id
            childImageSharp {
                fluid {
                    base64
                    tracedSVG
                    srcWebp
                    srcSetWebp
                    originalImg
                    originalName
                }
            }
        }
        allMarkdownRemark(
            limit: 2000
            sort: { fields: [frontmatter___date], order: DESC }
            filter: { frontmatter: { tags: { in: [$tag] } } }
        ) {
            totalCount
            edges {
                node {
                    excerpt
                    fields {
                        slug
                    }
                    frontmatter {
                        date(formatString: "MMMM DD, YYYY")
                        title
                        description
                        tags
                        thumbnail {
                            childImageSharp {
                                fluid(maxWidth: 1360) {
                                    ...GatsbyImageSharpFluid
                                }
                            }
                        }
                    }
                }
            }
        }
        imageSharp(fluid: { src: { regex: "/ogImage.jpg/" } }) {
            fluid {
                src
            }
        }
    }
`;
